import { StatisticsPath } from '@/apis/path'
import useStatsStore from '@/store/stats'
import { useFetch } from '@cetus/hooks'
import { addComma, symbolDataDisplayProcessing } from '@cetus/utils'

export default function useStatistics() {
  const { fetchByApi } = useFetch()
  const { setStatisticsData } = useStatsStore()

  const getStatistics = async () => {
    const res = await fetchByApi(StatisticsPath, 'GET')
    console.log('ðŸš€ ~ file: useStatistics.ts:8 ~ getStatistics ~ res:', res)

    if (res) {
      const result = {
        cumulativeVolume: symbolDataDisplayProcessing(res?.vol_in_usd, '$'),
        cumulativeVolume24h: symbolDataDisplayProcessing(res?.vol_in_usd_24h, '$'),
        totalValueLocked: symbolDataDisplayProcessing(res?.pure_tvl_in_usd, '$'),
        cumulativeTransactions: addComma(res?.tx_num),
        totalAccounts: addComma(res?.user_num)
      }
      setStatisticsData(result)
      return result
    }

    return null
  }

  return {
    getStatistics
  }
}
