import { useChartTime } from '@/hooks/common/useChartTime'
import useGetHistogramData, { GetHistogramDataParams } from '@/hooks/stats/useGetHistogramData'
import { SelectTab } from '@cetus/design'
import { symbolDataDisplayProcessing } from '@cetus/utils'
import { Box, HStack, Skeleton, Spinner, Text, VStack } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import VolumeChart from '../chart/VolumeChart'

export default function VolChartPageBlock({
  statisticsData,
  pageFrom = 'stats',
  isRefresh
}: {
  statisticsData: any
  pageFrom?: 'stats' | 'pools'
  isRefresh: boolean
}) {
  const chartTypeList = [
    {
      label: 'D'
    },
    {
      label: 'W'
    },
    {
      label: 'M'
    }
  ]
  const [titleDate, setTitleDate] = useState('(24H)')
  const [isLoading, setIsLoading] = useState(true)
  const [volType, setVolType] = useState<'D' | 'W' | 'M'>('D')
  // å›¾è¡¨æ•°æ®
  const { time, getHoverTime, getCurrentTime } = useChartTime()
  const { getHistogramData } = useGetHistogramData()
  const [volChartData, setVolChartData] = useState([])
  const [volCurrentData, setVolCurrentData] = useState(statisticsData?.cumulativeVolume24h)
  const [currentTime, setCurrentTime] = useState(time)

  const handleGetHistogramVolData = async () => {
    setIsLoading(true)
    const params: GetHistogramDataParams = {
      type: 'vol',
      date_type: volType == 'D' ? 'day' : volType == 'W' ? 'wek' : 'mon'
    }
    // é€‰æ‹©D é»˜è®¤ä¸ºè¿‘æœŸä¸€ä¸ªæœˆ é€‰æ‹©wï¼Œé»˜è®¤ä¸ºè¿‘æœŸ1å¹´ é€‰æ‹©M å¯å±•ç¤ºè¿‘5å¹´ï¼Œæ— æ•°æ®åˆ™ä¸å±•ç¤ºï¼Œå½“å‰æ—¶åˆ»å³å±•ç¤ºåŽ†å²å…¨éƒ¨æ•°æ®
    // if (pageFrom !== 'stats') {
    params['limit'] = volType == 'D' ? 31 : volType == 'W' ? 52 : 60
    // }
    const vol_res = await getHistogramData(params)
    console.log('ðŸš€ ~ file: TestData.tsx:78 ~ handleGetHistogramData ~ res:', vol_res)
    setVolChartData(vol_res)
    setIsLoading(false)
  }
  const handleChangeVol = (data: any) => {
    console.log('ðŸš€ ~ file: TestData.tsx:88 ~ handleChangeVol ~ data:', data)
    if (data) {
      getHoverTime(data.date, volType)
      setVolCurrentData(symbolDataDisplayProcessing(data.num, '$'))
      setTitleDate(volType == 'D' ? '(24H)' : volType == 'W' ? '(7D)' : '(30D)')
    } else {
      setTitleDate('(24H)')
      getCurrentTime()
      setVolCurrentData(statisticsData?.cumulativeVolume24h)
    }
  }
  useEffect(() => {
    if (isRefresh) {
      handleGetHistogramVolData()
    }
  }, [isRefresh])
  useEffect(() => {
    getCurrentTime()
  }, [])

  useEffect(() => {
    handleGetHistogramVolData()
  }, [volType])

  useEffect(() => {
    if (statisticsData) {
      setVolCurrentData(statisticsData?.cumulativeVolume24h)
    }
  }, [statisticsData])

  useEffect(() => {
    if (time) {
      console.log('ðŸš€ ~ useEffect ~ time:', time)
      setCurrentTime(time)
    }
  }, [time])
  return (
    <Box
      w={pageFrom == 'pools' ? '100%' : { base: '100%', lg: '50%' }}
      mt={{
        base: '12px',
        lg: '0'
      }}
    >
      <HStack w="100%" justify="space-between">
        <VStack align="flex-start" gap="4px">
          <Text fontWeight="500">Trading Volume {titleDate}</Text>
          <Skeleton isLoaded={!!volCurrentData} m="4px 0">
            <Text fontWeight={pageFrom == 'pools' ? '600' : '500'} fontSize="20px" color="text_caption">
              {volCurrentData}
            </Text>
          </Skeleton>
          {pageFrom == 'stats' && <Text fontWeight="500">{currentTime}</Text>}
        </VStack>
        <SelectTab
          type="outlineTab"
          tabList={chartTypeList}
          currentTab={volType}
          handleChangeTab={tab => setVolType(tab.label)}
          wrapStyle={{
            w: '128px',
            h: '32px',
            p: '3px',
            borderRadius: '8px'
          }}
          itemStyle={{
            flex: '1',
            fontSize: '14px',
            margin: '0px'
          }}
        />
      </HStack>

      <Box w="100%" h={pageFrom == 'pools' ? '270px' : '260px'} position="relative" pt={pageFrom == 'pools' ? '12px' : '20px'}>
        {isLoading && (
          <Box position="absolute" top="38%" left="50%" transform="translate(-50%,-50%)">
            <Spinner />
          </Box>
        )}
        <VolumeChart data={volChartData} onChangeValue={handleChangeVol} pageFrom={pageFrom} />
      </Box>
    </Box>
  )
}
